.advantages{
    background-color: var(--section-color1);
    
}

.advantages__title{
    display: none;
}
.advantages__card{
  padding-top: 60px;
  margin: 0 auto;
}
.advantages__list{
  display: flex;
  flex-wrap: wrap;
  gap: 28px;
  margin: 0 auto;
  border: 2px solid red;

  @media  screen and (min-width:320px) and (max-width:767px){
    // display: flex;
    // justify-content: center;
    // flex-wrap: wrap;
  }
  @media  screen and  (min-width:$tablet) and (max-width:1199px){
    display: inline-flex;
    flex-wrap: wrap;
    gap: 80px;
  } 
  @media  screen and (min-width:$desktop){
    display: inline-flex;
    flex-wrap: wrap;
    gap: 32px;  
  } 

}
.advantages__item{
  display: block;
  justify-content: center;
  list-style-type: none;
  width: 280px;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid red;
    
  @media  screen and (min-width:$mobile) and (max-width:767px){
    width: 440px;
  }
  @media  screen and  (min-width:$tablet) and (max-width:1199px){
    width: 152px;
    border: 2px solid blue;
  }
  @media  screen and (min-width:$desktop){
    margin: 0;
    width: 368px;
  } 
}

.advantages__thumb{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  margin: 0 auto;
  left: 0px;
  top: 0px;
  background: #F8D24E;
  border-radius: 25px;
}

.advantages__icon{
  width: 36px;
  height: 36px;
  text-align: center;
}

.advantages__pretitle{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.22;
  text-align: center;
  letter-spacing: 0.04em;
  color:var(--section-color2);
  margin-top: 22px;

  @media  screen and  (min-width:$tablet) and (max-width:1199px){
    font-size: 48px;
    line-height: 1.23;
    margin-top: 32px;
  }
  @media  screen and (min-width:$desktop){
    font-size: 58px;
    line-height: 1.22;
    margin-top: 40px;
  } 
}

.advantages__text{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.67;
  text-align: center;
  color:var(--section-color2);
  margin-top: 16px;

  @media  screen and  (min-width:$tablet) and (max-width:1199px){
    font-weight: 400;
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.02em;
    margin-top: 12px;
  }
  @media  screen and (min-width:$desktop){
    font-size: 16px;
    line-height: 1.75;
  } 
}
